.lists-block {
  background: #F8F8F8;
  padding: 8px 0px 40px 0px;
  .breadcrumbs {
    align-self: flex-start;
    margin-bottom: 20px;
  }
  .container {

  }
  .lists {
    width: 100%;
    display: block;
    @media screen and (max-width: $tablet) {
      display: flex;
    }
    .list {
      margin-bottom: 20px;
      @media screen and (max-width: $tablet) {
        margin-bottom: 0;
      }
      @media screen and (max-width: 640px) {
        width: 280px !important;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}